.topCat{
    position: relative;
    
}
.topCat img{
    width: 100%;
    height: 100%;
}
.topCat .nametop{
    position: relative;
    width: 100%;
}

.topCat .box{
    margin: 0 20px;
}
.topCat .nametop span{
    background-color:#0f3460 ;
    color: #fff;
    font-size: 12px;
    padding: 5px 10px;
    border-radius: 50px;
    margin: 5px;
    position: absolute;
    top: 0;
}
.topCat .nametop .tleft{
    left: 0;
}
.topCat .nametop .tright{
    background-color: aliceblue;
    color: #000;
    position: absolute;
    right: 0;

}